        ],
        idsBase: [],
        title: 'Users in Parkopedia',
        show: false,
        hide: true,
        editable: '',
        sortBy: 'name',
        sortDirection: 'asc',
        editableUserName: '',
        editableUserLastName: '',
        editableUserEmail: '',
        editableUserCountry: '',
        hideTools: false,
      }
    },  
    mounted() {
        // axios.get("http://localhost:3000/users")
        // .then(response => {this.usersBase = response.data;});    
    },
    methods:{
        sort(s){
          if(s === this.sortBy) {
            this.sortDirection = this.sortDirection === 'asc' ? 'desc' : 'asc';
          }
          this.sortBy = s;  
          return this.usersBase.sort((p1,p2) => {
            let modifier = 1;
            if(this.sortDirection === 'desc') modifier = -1;
            if(p1[this.sortBy] < p2[this.sortBy]) return -1 * modifier; if(p1[this.sortBy] > p2[this.sortBy]) return 1 * modifier;
            return 0;
          });
               
        },
        zerofill(i) {
          return (i < 10 ? '0' : '') + i;
        },   
        currentDateTime(){
          const current = new Date();
          const date = current.getFullYear()+'-'+ this.zerofill(current.getMonth()+1)+'-'+ this.zerofill(current.getDate());
          const dateTime = date;
          return dateTime;
        },
        disableEditing(index){
         this.usersBase[index].editable = false;
         this.hideTools = false; 
         this.editableUserName = "";
         this.editableUserLastName = "";
         this.editableUserEmail = ""; 
         this.editableUserCountry = "";  
        },
        saveEdit(index){
          this.usersBase[index]["first_name"] = this.editableUserName;
          this.usersBase[index]["last_name"] = this.editableUserLastName;
          this.usersBase[index]["email"] = this.editableUserEmail;
          this.usersBase[index]["country"] = this.editableUserCountry;
          this.usersBase[index]["modified"] = this.currentDateTime();
          this.disableEditing(index);
          
        },
        enableEditing(index){
          this.hideTools = true;   
          this.usersBase[index].editable = true;
          this.editableUserName = this.usersBase[index]["first_name"];
          this.editableUserLastName = this.usersBase[index]["last_name"];
          this.editableUserEmail = this.usersBase[index]["email"];
          this.editableUserCountry = this.usersBase[index]["country"];
        },
        creatUniqueId(){
          for(var i = 0; i < this.usersBase.length; i++ ){
            this.idsBase.push(this.usersBase[i]["id"]);
          }
          return Math.max(...this.idsBase) + 1; 
        },
        addNewUser(newUserName, newUserLastName, newUserEmail, newUserCountry){  
          this.usersBase.unshift({id: this.creatUniqueId(), first_name: newUserName, last_name: newUserLastName, email: newUserEmail, country: newUserCountry, modified: this.currentDateTime()} );
          this.hideUserAddTool();
        },
        showUserAddTool () {
          this.show = true;
          this.hide = false;
          this.hideTools = true;   
        },
        hideUserAddTool() {
          this.show = false;
          this.hideTools = false;   
          this.hide = true;
          this.newUserName = "";
          this.newUserLastName = "";
          this.newUserEmail = "";
          this.newUserCountry = "";
        },
        closeModal () {
            this.show = false;
        },
        removeUser(index){
          this.usersBase.splice(index, 1);
        },  
        
    },  
    computed:{
        
    },
}
</script>

